.denglu{
	position: fixed;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	background: rgba(0,0,0,0.5);
	z-index: 3;
}
.log_on{
	width: 420px;
	margin: 0 auto;
	background-color: #fff;
	position: relative;
	margin-top: 100px;
}
.log_on>.hide-loginform{
	padding:20px;
	position: absolute;
	right: 5px;
	top: 5px;

}
.log_on input{
	width: 331px;
	height: 44px;
	border: 1px solid #ccc;
	padding:0;
	margin: 0 auto;
	margin-left: 66px;
	text-indent: 10px;

}
.account{
    margin-top: 20px;
    position: relative;
}
.account a{
    border: 1px solid #ccc;
    display: block;
    width: 44px;
    height: 44px;
    position: absolute;
    left: 21px;
}
.account a img{
	margin-top: 9px;
	margin-left: 12px;
}
.account>div>span{
	display: block;
	font-size: 14px;
	color: red;
	padding-left: 21px;
}
.account>div>.one{
	display: none;
}
.log_on>.dl{
	display: block;
	width: 380px;
	line-height: 46px;
	background-color: #fff500;
	text-align: center;
	text-decoration: none;
	margin: 0 auto;
	margin-top: 53px;
	margin-bottom: 60px;
	color: #000;
	font-weight: bold;
	font-size: 22px;
}
.zhuce{
	padding-bottom: 20px;
	border-bottom: 3px solid #ccc;

}
.zhuce a{
   font-size: 14px;
   color: #000;
   text-decoration: none;
   margin-left: 22px;
}
.zhuce a:nth-child(2){
	float: right;
	margin-right: 22px;
}
.link_a{
	margin: 36px 0 19px 59px;
	padding-bottom: 20px;
}
.link_a a:nth-child(2){
   margin:0 107px;
}
.logo_one{
    margin:0 0 30px 107px;
    padding-top: 50px;
}



