.footerup{
	background-color: #000;
}
.footerup_on{
	width: 1200px;
	margin: 0 auto;
	padding-top: 60px;
	display: -ms-flexbox;
	display: flex;
}
.footerup_on>div{
}
.footerup_on>div h2{
	font-size: 23px;
	color: #fff;
	margin-bottom: 20px;
}
.footerup_on>div:nth-child(1){
	padding-left: 22px;
	width: 403px;
	margin-bottom: 108px;
}
.footerup_on div:nth-child(1)>div{
	display: -ms-flexbox;
	display: flex;
}
.footerup_on div:nth-child(1)>div>ul{
	margin-right: 42px;
}
.footerup_on div:nth-child(1)>div>ul img{
	width: 20px;
}
.footerup_on div:nth-child(1)>div>ul li{
	margin-bottom: 10px;
}
.footerup_on div:nth-child(1)>div>ul a{
	font-size: 20px;
	color: #ffd602;
}
.footerup_on div:nth-child(1)>div>ul a span{
	color: #fff;
	font-size: 16px;
}
.footerup_on>div:nth-child(2){
	width: 433px;
}
.footerup_on>div:nth-child(2)>div{
	padding-top: 6px;
	display: -ms-flexbox;
	display: flex;
}
.footerup_on div:nth-child(2)>div>div{
	width: 106px;
	margin-right: 30px;
}
.footerup_on div:nth-child(2)>div>div>img{
	display: block;
	max-width: 100%;
	margin-bottom: 10px;
}
.footerup_on div:nth-child(2)>div>div>a>img{
	width: 35px;
}
.footerup_on div:nth-child(2)>div>div:nth-child(2)>a{
	background-color: #01a90b;
}
.footerup_on div:nth-child(2)>div>div>a{
	display: block;
	border-radius: 50%;
	margin: 0 auto;
	width: 49px;
	background-color: #ff6501;
	font-size: 40px;
	line-height: 49px;
	color: #fff;
	text-align: center;
}
.footerup_on div:nth-child(3){
	width: 342px;
}
.footerup_on div:nth-child(3) h2{
	padding-top: 6px;
}
.footerup_on div:nth-child(3){
	font-size: 36px;
	color: #fff;
}
.footerup_on div:nth-child(3)>div{
	display: -ms-flexbox;
	display: flex;
}
.footerup_on div:nth-child(3)>div>div{
	color: #ffd800;
	margin-right: 20px;
}
.footerup_on div:nth-child(3)>div>div>img{
	width: 40px;
}
.footerup_on div:nth-child(3)>div>div>span{
	color: #fff;
	display: block;
}
.footerup_on div:nth-child(3)>div>div>span:nth-child(1){
	font-size: 18px;
}
.footerup_on div:nth-child(3)>div>div>span:nth-child(2){
	font-size: 14px;
}
.footerup_on div:nth-child(3)>div>use{
	color: #ffd800;
}
.footerdown{
	background-color: #2b2e33;
}
.footerdown_on{
	width: 1200px;
	margin: 0 auto;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: justify;
	    justify-content: space-between;
}
.footer_logo{
	-ms-flex: 1 1;
	    flex: 1 1;
	display: block;
	width: 190px;
	margin-left: 29px;
	margin-top: 15px;
	margin-bottom: 14px;
	padding-top: 61px;
	background: url(./images/logo_03.png) no-repeat;
}
.copyright{
	font-size: 12px;
	color: #717479;
	line-height: 17px;
	margin-top: 30px;
}



