.mains_go{
	width: 500px;
	margin: 0 auto;

}
.mains_top div p:nth-child(1){
	margin: 0;
    margin-right: 20px;
}
.mains_mid{
	width: 1200px;
	margin: 0 auto;
	background-color: #fff;
	padding-bottom: 80px;
}
.min_mid{
	width: 1100px;
	margin: 0 auto;

}
input{
	width: 26px;
	height: 26px;
}
.min_mid>div>p{
	float: left;
	line-height: 33px;
	margin-bottom: 16px;
}
.min_mid>div>p:nth-child(1){
}
.min_mid>div>p:nth-child(1)>span{
	line-height: 33px;
	display: inline-block;
	margin-top: -3px;
	vertical-align:center;
}
.min_mid>div>p:nth-child(2){
	margin-left: 30px;
	margin-right: 382px;
}
.min_mid>div>p:nth-child(4){
	margin-left: 124px;
	margin-right: 170px;
}
.min_mid>div>p:nth-child(6){
	float: right;
	margin-right: 17px;
}
.mains_mid ul{
	border-top: 2px solid #2b2e33;
	border-bottom: 2px solid #2b2e33;
	list-style: none;
}
.mains_mid ul li{
	border-bottom: 1px solid #ccc;
	position: relative;
}
.left{
	width: 427px;
	float: left;
	margin: 20px 0;
}
.left input{
	float: left;
	margin: 35px 20px;
}
.left img{
	float: left;
	margin-left: 28px;
	margin-right: 10px;
}
.left>a{
	float: left;
	display: block;
	text-decoration: none;
	font-size: 14px;
	line-height: 18px;
	margin-bottom: 16px;
	color: #000;
}
.left span{
    font-size: 12px;
    color: #ccc;
    line-height: 17px;
}
.right{
	width: 575px;
	float: right;
	margin:58px 0 60px 0;
	margin-right: 26px;
}
.qipao{
	background-image: url('./images/tips_03.png');
	background-repeat: no-repeat;
	width: 206px;
	position: absolute;
	top:86px;
	right: -3px;
}
.no{
	display: block;
	float: left;
	text-decoration: none;
	font-size: 14px;
	color: #fff;
	background-color: #f50000;
    width: 74px;
    line-height: 32px;
	margin-top: 11px;
	text-align: center;
	margin-left: 4px;
}
.off{
	display: block;
	text-decoration: none;
	float: left;
	font-size: 14px;
	color: #fff;
	background-color: #2b2e33;
	width: 126px;
	line-height: 32px;
	text-align: center;
	margin-top: 11px;

}
.right div{
	float: left;

}
.right em{
	line-height: 36px;
}
.right div:nth-child(2){
	margin:0 58px 0 30px;
}
.right div:nth-child(3)>span{
	color: red;
	display: inline-block;
	width: 92px;
	line-height: 36px;
	text-align: right;
}
.right div input{
    padding:0;
    text-align: center;
    float: left;
    width: 38px;
    height: 38px;
    border: 0;
    margin:0 34px;
}
.right>div>a{
	display: block;
    border: 1px solid #000;
    text-align: center;
    width: 36px;
    line-height: 36px;
    float: left;
    text-decoration: none;
}
.right>a{
	margin-left: 145px;
}
.mains_bottom{
	background-color: #2b2e33;
	margin-top: 50px;

}
.bottom_left{
	float: left;
	position: relative;
}
.qipao_a{
    background-image: url('./images/tips1_03.png');
	background-repeat: no-repeat;
	width: 206px;
	position: absolute;
	top:-38px;
	right: 65px;
}
.no_a{
	display: block;
	float: left;
	text-decoration: none;
	font-size: 14px;
	color: #fff;
	background-color: #f50000;
    width: 74px;
    line-height: 32px;
	text-align: center;
	margin-left: 4px;
	margin-top: 3px;
	margin-bottom: 11px;
}
.off_a{
	display: block;
	text-decoration: none;
	float: left;
	font-size: 14px;
	color: #fff;
	background-color: #2b2e33;
	width: 126px;
	line-height: 32px;
	text-align: center;
	margin-top: 3px;

}
.bottom_left>a{
    background-image: url('./images/lj_07.jpg');
    background-repeat: no-repeat;
    margin-left: 14px;
    padding-left: 21px;
    line-height: 60px;
    background-position: 0 2px;
    color: #fff;
    padding-right: 20px;
    border-right: 2px solid #6f7277;
}
.bottom_left p{
	float: none;
}
.bottom_left em{
	margin-left: 15px;
	color: #fff;
}
.bottom_left span{
	color: #c8ae13;
}
.bottom_right{
	float: right;
}
.bottom_right div{
	float: left;
	margin-top: 10px;

}
.bottom_right strong{
	font-size:34px;
	line-height: 60px;
	color:#fdd900;
}
.bottom_right a{
	float: right;
	display: block;
	width: 220px;
	font-size: 26px;
	line-height: 60px;
	text-align: center;
	text-decoration: none;
	color: #000;
	background-color: #fdd900;
	margin-left: 20px;
}
.bottom_right div p{
	font-size: 14px;
	line-height: 18px;
	color: #ccc;
}
.mains_top{
	width: 1200px;
	margin: 0 auto;
	background-color: #fdd900;
	padding-top: 24px;

}
.mains_top h2{
	font-size: 30px;
	line-height: 60px;
	border-left: 5px solid #000;
	float: left;
	margin-right: 12px;
	padding-left: 19px;
}
.mains_top p{
	float: left;
	font-size: 24px;
	line-height: 60px;
	margin-left: 20px;
}
.mains_top span{
	color: red;
}
.mains_top div:nth-child(1){
	margin:0 0 15px 50px;
	float: left;
}
.mains_top div:nth-child(2){
	float: right;
	margin-right: 50px;
}
.mains_top div:nth-child(2) p{
	border-bottom: 2px solid #000;
}
.mains_top div em{
	float: left;
	font-size: 24px;
	line-height: 60px;
    font-weight: bold;
    border-bottom: 5px solid #000;
}
.mains_top div img{
	margin-bottom: -4px;
}



